// Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØ
const fetchUserInfo = async () => {
  try {
    if (userStore.hasUserInfo) {
      userInfo.value = userStore.userInfo
    } else {
      const data = await userStore.fetchUserInfo()
      userInfo.value = data
    }
  } catch (error) {
